"use client"

import { useState } from "react"
import Image from "next/image"
import { Plus, Search, Edit2, Trash2, X, Upload, ChevronLeft, ChevronRight } from "lucide-react"
import { mockProducts, mockCategories, mockSizes, mockColors } from "@/lib/mock-data"

interface Product {
  id: string
  nome: string
  preco: number
  precoPromocional: number | null
  categoria: string
  categoriaId: string
  tamanhos: string[]
  cores: string[]
  status: string
  personalizavel: boolean
  descricao: string
  imagens: string[]
  createdAt: string
}

export default function AdminProdutos() {
  const [products, setProducts] = useState<Product[]>(mockProducts)
  const [searchTerm, setSearchTerm] = useState("")
  const [filterCategoria, setFilterCategoria] = useState("")
  const [showModal, setShowModal] = useState(false)
  const [editingProduct, setEditingProduct] = useState<Product | null>(null)
  const [formData, setFormData] = useState({
    nome: "",
    preco: "",
    precoPromocional: "",
    categoriaId: "",
    tamanhos: [] as string[],
    cores: [] as string[],
    descricao: "",
    status: "ativo",
    personalizavel: false,
  })

  const filteredProducts = products.filter((p) => {
    const matchSearch =
      p.nome.toLowerCase().includes(searchTerm.toLowerCase())
    const matchCategoria = !filterCategoria || p.categoriaId === filterCategoria
    return matchSearch && matchCategoria
  })

  const openCreateModal = () => {
    setEditingProduct(null)
    setFormData({
      nome: "",
      preco: "",
      precoPromocional: "",
      categoriaId: "",
      tamanhos: [],
      cores: [],
      descricao: "",
      status: "ativo",
      personalizavel: false,
    })
    setShowModal(true)
  }

  const openEditModal = (product: Product) => {
    setEditingProduct(product)
    setFormData({
      nome: product.nome,
      preco: product.preco.toString(),
      precoPromocional: product.precoPromocional?.toString() || "",
      categoriaId: product.categoriaId,
      tamanhos: product.tamanhos || [],
      cores: product.cores || [],
      descricao: product.descricao,
      status: product.status,
      personalizavel: product.personalizavel || false,
    })
    setShowModal(true)
  }

  const handleSave = () => {
    if (editingProduct) {
      setProducts(
        products.map((p) =>
          p.id === editingProduct.id
            ? {
                ...p,
                nome: formData.nome,
                preco: Number.parseFloat(formData.preco),
                precoPromocional: formData.precoPromocional ? Number.parseFloat(formData.precoPromocional) : null,
                categoriaId: formData.categoriaId,
                categoria: mockCategories.find((c) => c.id === formData.categoriaId)?.nome || "",
                tamanhos: formData.tamanhos,
                cores: formData.cores,
                descricao: formData.descricao,
                status: formData.status,
                personalizavel: formData.personalizavel,
              }
            : p,
        ),
      )
    } else {
      const newProduct: Product = {
        id: (products.length + 1).toString(),
        nome: formData.nome,
        preco: Number.parseFloat(formData.preco),
        precoPromocional: formData.precoPromocional ? Number.parseFloat(formData.precoPromocional) : null,
        categoriaId: formData.categoriaId,
        categoria: mockCategories.find((c) => c.id === formData.categoriaId)?.nome || "",
        tamanhos: formData.tamanhos,
        cores: formData.cores,
        descricao: formData.descricao,
        status: formData.status,
        personalizavel: formData.personalizavel,
        imagens: ["/new-product-launch.png"],
        createdAt: new Date().toISOString().split("T")[0],
      }
      setProducts([...products, newProduct])
    }
    setShowModal(false)
  }

  const handleDelete = (id: string) => {
    if (confirm("Tem certeza que deseja excluir este produto?")) {
      setProducts(products.filter((p) => p.id !== id))
    }
  }

  return (
    <div className="p-8">
      {/* Header */}
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-light text-foreground">Produtos</h1>
          <p className="text-foreground/60">Gerencie seu catálogo de produtos</p>
        </div>
        <button
          onClick={openCreateModal}
          className="flex items-center gap-2 bg-primary text-primary-foreground px-4 py-2 rounded-lg font-medium hover:bg-primary/90 transition-colors"
        >
          <Plus size={18} />
          Novo Produto
        </button>
      </div>

      {/* Filters */}
      <div className="flex flex-col sm:flex-row gap-4 mb-6">
        <div className="relative flex-1">
          <Search size={18} className="absolute left-3 top-1/2 -translate-y-1/2 text-foreground/50" />
          <input
            type="text"
            placeholder="Buscar por nome..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full pl-10 pr-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
          />
        </div>
        <select
          value={filterCategoria}
          onChange={(e) => setFilterCategoria(e.target.value)}
          className="px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
        >
          <option value="">Todas as categorias</option>
          {mockCategories.map((cat) => (
            <option key={cat.id} value={cat.id}>
              {cat.nome}
            </option>
          ))}
        </select>
      </div>

      {/* Table */}
      <div className="bg-card border border-border rounded-xl overflow-hidden">
        <div className="overflow-x-auto">
          <table className="w-full">
            <thead className="bg-muted border-b border-border">
              <tr>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Produto</th>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Preço</th>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Categoria</th>
                <th className="px-6 py-4 text-left text-sm font-medium text-foreground">Status</th>
                <th className="px-6 py-4 text-center text-sm font-medium text-foreground">Ações</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-border">
              {filteredProducts.map((product) => (
                <tr key={product.id} className="hover:bg-muted/50 transition-colors">
                  <td className="px-6 py-4">
                    <div className="flex items-center gap-3">
                      <div className="w-12 h-12 rounded-lg bg-muted overflow-hidden">
                        <Image
                          src={product.imagens[0] || "/placeholder.svg"}
                          alt={product.nome}
                          width={48}
                          height={48}
                          className="w-full h-full object-cover"
                        />
                      </div>
                      <span className="font-medium text-foreground">{product.nome}</span>
                    </div>
                  </td>
                  <td className="px-6 py-4">
                    <div>
                      {product.precoPromocional ? (
                        <>
                          <span className="text-sm text-foreground/50 line-through">R$ {product.preco.toFixed(2)}</span>
                          <span className="ml-2 font-medium text-accent">R$ {product.precoPromocional.toFixed(2)}</span>
                        </>
                      ) : (
                        <span className="font-medium text-foreground">R$ {product.preco.toFixed(2)}</span>
                      )}
                    </div>
                  </td>
                  <td className="px-6 py-4 text-sm text-foreground/70">{product.categoria}</td>
                  <td className="px-6 py-4">
                    <span
                      className={`px-2 py-1 rounded-full text-xs font-medium ${
                        product.status === "ativo" ? "bg-green-100 text-green-700" : "bg-gray-100 text-gray-700"
                      }`}
                    >
                      {product.status === "ativo" ? "Ativo" : "Inativo"}
                    </span>
                  </td>
                  <td className="px-6 py-4">
                    <div className="flex items-center justify-center gap-2">
                      <button
                        onClick={() => openEditModal(product)}
                        className="p-2 text-foreground/60 hover:text-primary hover:bg-muted rounded-lg transition-colors"
                      >
                        <Edit2 size={16} />
                      </button>
                      <button
                        onClick={() => handleDelete(product.id)}
                        className="p-2 text-foreground/60 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                      >
                        <Trash2 size={16} />
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        <div className="flex items-center justify-between px-6 py-4 border-t border-border">
          <p className="text-sm text-foreground/60">
            Mostrando {filteredProducts.length} de {products.length} produtos
          </p>
          <div className="flex items-center gap-2">
            <button className="p-2 border border-border rounded-lg hover:bg-muted transition-colors">
              <ChevronLeft size={16} />
            </button>
            <button className="px-3 py-1 bg-primary text-primary-foreground rounded-lg text-sm">1</button>
            <button className="px-3 py-1 hover:bg-muted rounded-lg text-sm">2</button>
            <button className="p-2 border border-border rounded-lg hover:bg-muted transition-colors">
              <ChevronRight size={16} />
            </button>
          </div>
        </div>
      </div>

      {/* Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-card border border-border rounded-xl w-full max-w-2xl max-h-[90vh] overflow-y-auto">
            <div className="flex items-center justify-between p-6 border-b border-border">
              <h2 className="text-xl font-medium text-foreground">
                {editingProduct ? "Editar Produto" : "Novo Produto"}
              </h2>
              <button onClick={() => setShowModal(false)} className="text-foreground/60 hover:text-foreground">
                <X size={20} />
              </button>
            </div>

            <div className="p-6 space-y-4">
              {/* Image Upload */}
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">Imagens</label>
                <div className="border-2 border-dashed border-border rounded-lg p-8 text-center hover:border-accent transition-colors cursor-pointer">
                  <Upload size={32} className="mx-auto text-foreground/40 mb-2" />
                  <p className="text-sm text-foreground/60">Clique ou arraste imagens aqui</p>
                  <p className="text-xs text-foreground/40 mt-1">PNG, JPG até 5MB</p>
                </div>
              </div>

              <div className="grid grid-cols-1 gap-4">
                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">Nome do Produto</label>
                  <input
                    type="text"
                    value={formData.nome}
                    onChange={(e) => setFormData({ ...formData, nome: e.target.value })}
                    className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">Preço (R$)</label>
                  <input
                    type="number"
                    step="0.01"
                    value={formData.preco}
                    onChange={(e) => setFormData({ ...formData, preco: e.target.value })}
                    className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">Preço Promocional</label>
                  <input
                    type="number"
                    step="0.01"
                    value={formData.precoPromocional}
                    onChange={(e) => setFormData({ ...formData, precoPromocional: e.target.value })}
                    className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                    placeholder="Opcional"
                  />
                </div>
              </div>

              <div className="grid grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">Categoria</label>
                  <select
                    value={formData.categoriaId}
                    onChange={(e) => setFormData({ ...formData, categoriaId: e.target.value })}
                    className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  >
                    <option value="">Selecione...</option>
                    {mockCategories.map((cat) => (
                      <option key={cat.id} value={cat.id}>
                        {cat.nome}
                      </option>
                    ))}
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium text-foreground mb-2">Status</label>
                  <select
                    value={formData.status}
                    onChange={(e) => setFormData({ ...formData, status: e.target.value })}
                    className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  >
                    <option value="ativo">Ativo</option>
                    <option value="inativo">Inativo</option>
                  </select>
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-foreground mb-2">Tamanhos</label>
                <div className="flex flex-wrap gap-2 p-2 border border-border rounded-lg bg-background min-h-[42px]">
                  {mockSizes.map((size) => (
                    <label
                      key={size.id}
                      className={`flex items-center gap-1 px-2 py-1 rounded cursor-pointer text-sm border transition-colors ${
                        formData.tamanhos.includes(size.id)
                          ? "bg-primary/10 border-primary text-primary"
                          : "bg-muted border-transparent text-foreground/70 hover:bg-muted/80"
                      }`}
                    >
                      <input
                        type="checkbox"
                        className="hidden"
                        checked={formData.tamanhos.includes(size.id)}
                        onChange={(e) => {
                          const newSizes = e.target.checked
                            ? [...formData.tamanhos, size.id]
                            : formData.tamanhos.filter((id) => id !== size.id)
                          setFormData({ ...formData, tamanhos: newSizes })
                        }}
                      />
                      {size.nome}
                    </label>
                  ))}
                </div>
              </div>

              <div>
                <label className="block text-sm font-medium text-foreground mb-2">Cores</label>
                <div className="flex flex-wrap gap-2 p-2 border border-border rounded-lg bg-background min-h-[42px]">
                  {mockColors.filter((c) => c.ativo).map((color) => (
                    <label
                      key={color.id}
                      className={`flex items-center gap-2 px-3 py-2 rounded cursor-pointer text-sm border transition-colors ${
                        formData.cores.includes(color.id)
                          ? "bg-primary/10 border-primary text-primary"
                          : "bg-muted border-transparent text-foreground/70 hover:bg-muted/80"
                      }`}
                    >
                      <input
                        type="checkbox"
                        className="hidden"
                        checked={formData.cores.includes(color.id)}
                        onChange={(e) => {
                          const newColors = e.target.checked
                            ? [...formData.cores, color.id]
                            : formData.cores.filter((id) => id !== color.id)
                          setFormData({ ...formData, cores: newColors })
                        }}
                      />
                      <div
                        className="w-4 h-4 rounded-full border border-border"
                        style={{ backgroundColor: color.codigoHex }}
                      />
                      {color.nome}
                    </label>
                  ))}
                </div>
              </div>

              <div className="flex items-center gap-2 pt-4">
                <input
                  type="checkbox"
                  id="personalizavel"
                  checked={formData.personalizavel}
                  onChange={(e) => setFormData({ ...formData, personalizavel: e.target.checked })}
                  className="w-4 h-4 rounded border-border text-primary focus:ring-primary"
                />
                <label htmlFor="personalizavel" className="text-sm font-medium text-foreground cursor-pointer">
                  Produto Personalizável
                </label>
              </div>

              <div>
                <label className="block text-sm font-medium text-foreground mb-2">Descrição</label>
                <textarea
                  rows={3}
                  value={formData.descricao}
                  onChange={(e) => setFormData({ ...formData, descricao: e.target.value })}
                  className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent resize-none"
                />
              </div>
            </div>

            <div className="flex items-center justify-end gap-3 p-6 border-t border-border">
              <button
                onClick={() => setShowModal(false)}
                className="px-4 py-2 border border-border rounded-lg hover:bg-muted transition-colors"
              >
                Cancelar
              </button>
              <button
                onClick={handleSave}
                className="px-4 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors"
              >
                {editingProduct ? "Salvar Alterações" : "Criar Produto"}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}
